<template>
	<view class="content">
		<view class="list-view">
			<view class="list-item" v-for="item in patientList">
				<view class="header">
					<view class="list-head-left">
						<image class="patient-pic" :src="item.pic" mode="aspectFill"></image>
						<text>{{item.name}} {{item.sex}}</text>
						<view class="patient-status">
							<text v-show="item.isfinish">已完成</text>
						</view>
					</view>
					<view class="list-head-right">
						<text>ID: {{item.credentials_number}}</text>
						<text>透析号: {{item.hemodialysis_id}}</text>
						<text>治疗项目: {{item.treatmentitems}}</text>
						<text>传染病: {{item.infectious_check_result}}</text>
						<text>净化器: {{item.filter}}</text>
						<text>预订时间: {{item.frequency_hours}}小时</text>
						<text>预计脱水: {{item.dry_water_value}}Kg</text>
						<text>抗凝方式: {{item.anticoagulationmethod}}</text>
						<text>首计: {{item.first}}</text>
						<text>追加: {{item.second}}</text>
					</view>
				</view>
				<view v-show="!item.isfinish" class="footer">
					<button class="btn" @click="takePic(item)">患者拍照</button>
					<button class="btn" @click="doTreatment(item)">执行治疗</button>
					<button class="btn" @click="endTreatment(item)">结束治疗</button>
				</view>
			</view>
		</view>
		<view v-show="navSelected" class="nav-select">
			<view class="nav-select-item" @click="changeAccount">切换账号</view>
			<view class="nav-select-item" @click="reflesh">刷新</view>
		</view>
	</view>
</template>

<script>
	import _ from "lodash"
	export default {
		data() {
			return {
				navSelected:false,
				patientList:[],
			}
		},
		onNavigationBarButtonTap(e){
			this.navSelected = !this.navSelected;
		},
		onBackPress() {
			uni.navigateTo({
				url:"../search-select/search-select",
			});
			return true;
		},
		onLoad() {
			let patientData = uni.getStorageSync("patientList");
			let timer = 0
			for (let i = 0; i < patientData.length; i++) {
				//获取患者头像
				this.$myRequest({
					url:'/patient/img',
					method:'POST',
					data:{
						hemodialysis_id:patientData[i].hemodialysis_id,
					},
					success: (res) => {
						if(res.data.code == 200){
							patientData[i].pic = res.data.data.pat_pic;
						}
						clearTimeout(timer)
						timer = setTimeout(()=>{
							this.patientList =  _.cloneDeep(patientData);
						},300)
					},
				});
			}
		},
		methods: {
			//患者拍照
			takePic(item){
				uni.chooseImage({
					count: 1,
					success: function (res) {
						console.log("success",res);
					},
					fail: function (err) {
						console.log("err",err);
					}
				});
				
			},
			//执行治疗按钮方法
			doTreatment(item){
				uni.setStorageSync("patient",item);
				uni.switchTab({
					url: "../treat-info/treat-info",
				});
			},
			//结束治疗按钮方法
			endTreatment(item){
				if(item.isfinish == false){
					item.isfinish = true;
				}
			},
			//导航栏右侧切换账号
			changeAccount(){
				uni.navigateTo({
					url:"../login/login",
				});
				this.patientList = [];
			},
			////导航栏右侧刷新
			reflesh(){
				this.navSelected = false;
				let searchInfo = uni.getStorageSync("searchInfo");
				uni.showToast({
					title: 'loading',
					icon: 'loading',
					mask: true
				});
				this.$myRequest({
					url:'/patient/patientlist',
					method:'POST',
					data:{
						banci_id:searchInfo.banci_id,
						date:searchInfo.date,
						room:searchInfo.room,
					},
					success: (res) => {
						// console.log(res);
						if(res.data.code == 200){
							uni.setStorageSync("patientList",res.data.data);
							console.log("刷新接口")
							let patientData = res.data.data;
							let timer = 0
							for (let i = 0; i < patientData.length; i++) {
								//获取患者头像
								this.$myRequest({
									url:'/patient/img',
									method:'POST',
									data:{
										hemodialysis_id:patientData[i].hemodialysis_id,
									},
									success: (res) => {
										if(res.data.code == 200){
											patientData[i].pic = res.data.data.pat_pic;
										}
										clearTimeout(timer)
										timer = setTimeout(()=>{
											this.patientList =  _.cloneDeep(patientData);
										},300)
									},
								});
							}

						}
					},
				});
			}
		}
	}
</script>

<style>
	@import url("../../static/css/style.css");	
	.list-view{
		width: 100%;
	}
	.list-item{
		padding: 20rpx;
		margin-bottom: 20rpx;
		width: calc(100% - 40rpx);
		display: flex;
		flex-direction: column;
		border-radius: 20rpx;
		box-shadow: 1px 2px 5px rgba(28, 42, 134, 0.4);
	}
	.header{
		display: flex;
		flex-direction: row;
	}
	.list-head-left{
		display: flex;
		flex-direction: column;
		align-items: center;
		text-align: center;
	}	
	.patient-pic{
		width: 200rpx;
		height: 300rpx;
	}
	.patient-status{
		margin-top: 5rpx;
		width: 120rpx;
		color: #FFFFFF;
		background-color: #C0C0C0;
		border-radius: 20rpx;
		font-size: 28rpx;
	}
	.list-head-right{
		margin-left: 20rpx;
		flex: 1;
		display: flex;
		flex-direction: column;
		font-size: 28rpx;
	}
	.footer{
		margin-top: 20rpx;
		border-top: 2rpx solid;
		border-color: #51D3C7;
		padding-top: 20rpx;
		display: flex;
		justify-content: center;
	}
	button::after{
		border: none;
	}
	.btn {
	    display: flex;
	    align-items: center;
	    justify-content: center;
		background-color: #51D3C7;
		/* border: 2rpx solid; */
	    /* border-color: #51D3C7; */
		color: #FFFFFF;
		font-size: 28upx;
	    border-radius: 20rpx;
	    box-shadow: 1px 2px 5px rgba(28, 42, 134, 0.4);
		height: 50rpx;
	}
	.nav-select{
		position: fixed;
		z-index: 100;
		top: 90rpx;
		right: 5rpx;
		background-color: #EEEEEE;
		width: 250rpx;
		/* height: 100rpx; */
		box-shadow: 2px 2px 5px rgba(28, 42, 134, 0.4);
	}
	.nav-select-item{
		height: 50rpx;
		padding: 10rpx;
		border-left: 1rpx solid;
		border-right: 1rpx solid;
		border-bottom: 1rpx solid;
		border-color: #C0C0C0;
	}
</style>
